.context {
  position: relative;
  padding: 3px 5px;
  cursor: default;
  flex-grow: 1;
  overflow: hidden;
  font-family: "consolas", "lucida console", "courier new", monospace;
}
.context:hover {
  background-color: var(--contextHoverBg);
}
.context .contextTag {
  font-weight: bold;
  color: var(--elementTagNameColor);
}
.context .contextId {
  color: var(--elementIdColor);
}
.context .contextClasses {
  color: var(--elementClassesColor);
}
.context .contextActions {
  display: none;
  position: absolute;
  box-sizing: border-box;
  right: 0px;
  top: 0px;
  height: 100%;
  padding: 0em 1em;
  align-items: center;
  background-color: var(--contextHoverBg);
}
.context:hover .contextActions {
  display: flex;
}
.context .contextActions i {
  padding: 5px;
  cursor: pointer;
}
.context .contextActions i > svg {
  fill: var(--icon);
}
.context .contextActions i:hover > svg {
  fill: var(--iconHover);
}