:global #root {
  --pageBg: #fff;
  --bodyColor: #333;
  --bodyPrimaryColor: #111;
  --borderColor: #ccc;
  --optionBarBackground: #f3f3f3;
  --icon: #777;
  --iconHover: #333;
  --iconActive: rgb(138 180 248);
  --contextBg: transparent;
  --contextHoverBg: #eee;
  --contextSelectedBg: #cfe8fc;
  --elementTagNameColor: rgb(0, 0, 170);
  --elementIdColor: rgb(255, 0, 0);
  --elementClassesColor: rgb(0, 136, 0);
  --elementInertColor: #333;
  --tagBg: #ddd;
  --btnBg: #fff;
  --btnColor: #333;
  --btnBorderColor: #ccc;
  --btnHoverBg: #eee;
  --btnHoverColor: #000;
}
:global #root.theme-dark {
  --pageBg: hsl(225, 6%, 13%);
  --bodyColor: #aaa;
  --bodyPrimaryColor: rgb(232, 234, 237);
  --borderColor: #3d3d3d;
  --optionBarBackground: hsl(225, 5%, 17%);
  --icon: #888;
  --iconHover: #bbb;
  --iconActive: rgb(138 180 248);
  --contextBg: transparent;
  --contextHoverBg: rgb(34, 42, 54);
  --contextSelectedBg: rgb(7, 61, 105);
  --elementTagNameColor: rgb(93, 176, 215);
  --elementIdColor: rgb(230, 130, 130);
  --elementClassesColor: rgb(242, 151, 102);
  --elementInertColor: rgb(192, 192, 192);
  --tagBg: rgb(34, 42, 54);
  --scrollbarCornerBg: #242424;
  --scrollbarThumbBg: #333;
  --scrollbarTrackBg: rgb(36,36,36);
  --btnBg: hsl(225, 6%, 13%);
  --btnColor: #aaa;
  --btnBorderColor: #3d3d3d;
  --btnHoverBg: rgb(34, 42, 54);
  --btnHoverColor: #fff;
}
:global #root {
  color: var(--bodyColor);
  background-color: var(--pageBg);
  height: 100%;
  overflow: auto;
}
:global #root.theme-dark::-webkit-scrollbar,
:global #root.theme-dark *::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
:global #root.theme-dark::-webkit-scrollbar-corner,
:global #root.theme-dark *::-webkit-scrollbar-corner {
  background-color: var(--scrollbarCornerBg);
}
:global #root.theme-dark::-webkit-scrollbar-thumb,
:global #root.theme-dark *::-webkit-scrollbar-thumb {
  background-color: var(--scrollbarThumbBg);
  box-shadow: inset 0 0 1px rgba(255, 255, 255, 0.5);
}
:global #root.theme-dark::-webkit-scrollbar-track,
:global #root.theme-dark *::-webkit-scrollbar-track {
  background-color: var(--scrollbarTrackBg);
  box-shadow: inset 0 0 1px rgba(255, 255, 255, 0.3);
}
:global #root.theme-dark input[type=checkbox] {
  filter: invert(1);
}

html, body {
  height: 100%;
  margin: 0px;
}

body {
  font-size: 12px;
  font-family: "Segoe UI", Tahoma, sans-serif;
}

:global(.inline-icon) svg {
  width: 15px;
  height: 15px;
  vertical-align: text-bottom;
}

:global(.btn) {
  font-size: 12px;
  background-color: var(--btnBg);
  color: var(--btnColor);
  padding: 4px 9px;
  border: solid 1px var(--btnBorderColor);
  border-radius: 4px;
  cursor: pointer;
}

:global(.btn):hover {
  background-color: var(--btnHoverBg);
  color: var(--btnHoverColor);
}